








const importAll = (requireContext: __WebpackModuleApi.RequireContext) => requireContext.keys().forEach(requireContext)
try {
    importAll(require.context('@/assets/icons', true, /\.svg$/))//将一个目录里面所有的文件引入到当前文件
} catch (error) {
    console.log(error)
}
export default {
    name: 'Icon',
    components: {},

    props: {
        name: {
            type: String,
            default() {
                return 'money'
            }
        }
    },

    data() {
        return {}
    },


    methods: {}
}
